@import "null";

body {
	line-height: 22px;
	letter-spacing: 0px;
	color: $textColorBase;
	background-color: $colorAlternate;
	scroll-behavior: smooth;

	&._lock {
		overflow: hidden;
	}
}

.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.page {
	flex: 1 1 auto;
}

._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;

	@media (max-width: ($md1+px)) {
		max-width: 970px;
	}

	@media (max-width: ($md2+px)) {
		max-width: 750px;
	}

	@media (max-width: ($md3+px)) {
		max-width: none;
		padding: 0 22px;
	}
}
._container--small {
	max-width: 1414px;
}
